@font-face {
  font-family: "pt_mono";
  src: url("../fonts/pt_mono-regular.woff2") format("woff2"), url("../fonts/pt_mono-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "roboto_condensed";
  src: url("../fonts/roboto_condensed-regular.woff2") format("woff2"), url("../fonts/roboto_condensed-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "roboto_condensed";
  src: url("../fonts/roboto_condensed-bold.woff2") format("woff2"), url("../fonts/roboto_condensed-bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

p, li, span {
  font-family: "pt_mono", monospace;
}

h1, h2, h3, h4, h5 {
  font-family: "roboto_condensed", sans-serif;
  font-weight: normal;
}

button {
  font-family: "roboto_condensed", sans-serif;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.form {
  label {
  font-family: "pt_mono";
  .font_14_16;
  vertical-align: middle;
  }
}

.popup__form  {
  .popup__cancel {
  font-size: 24px;
  font-weight: normal;
  letter-spacing: 0;
  }

  h3 {
  .font_28_39;
  }

  p {
  .font_16_22;
  }
}

.menu span {
  font-size: 16px;
  line-height: 18px;
}

.menu__button {
  .font_14_16;
  text-align: center;
  letter-spacing: 0.05em;
}

.header h1 {
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;

  @media (min-width: 768px) and (max-width: 1023px) {
  font-size: 60px;
  line-height: 70px;
  }

  @media (max-width: 767px) {
  font-size: 40px;
  line-height: 47px;
  }
}

.header p {
  font-size: 22px;
  line-height: 34px;

  @media (min-width: 768px) and (max-width: 1023px) {
  font-size: 18px;
  line-height: 28px;
  }

  @media (max-width: 767px) {
  font-size: 16px;
  line-height: 25px;
  }
}

.header__button {
  font-size: 16px;
  line-height: 19px;
}

.header__item a>p {
  .font_14_16;
}

.advantages__item h4 {
  font-size: 24px;
  line-height: 34px;

  @media (max-width: 767px) {
  font-size: 20px;
  line-height: 28px;
  }
}

.advantages__item p {
  .font_16_22;
}

.about-company_text h3 {
  .font_36_51;

  @media (min-width: 768px) and (max-width: 1023px) {
  .font_32_45;
  }

  @media (max-width: 767px) {
  .font_28_39;
  }
}

.about-company_text p {
  .font_16_22;
}

.about-company__button {
  .font_14_16;
}

.services h2 {
  .font_36_51;
  text-align: left;

  @media (min-width: 768px) and (max-width: 1023px) {
  .font_32_45;
  }

  @media (max-width: 767px) {
  .font_28_39;
  }
}

.services__images p {
  font-family: "roboto_condensed", sans-serif;
  font-size: 20px;
  line-height: 30px;
}

.services__button {
  font-size: 24px;
  font-weight: normal;
}

.consult h3 {
  .font_36_51;
}

.consult p {
  .font_16_22;
}

.consult__button {
  .font_14_16;
}

.footer__top-container {
  font-size: 15px;

  h4 {
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  }

  p {
  line-height: 16px;
  }
}

.footer__top-item_mobile {
  .accordion {
  font-weight: normal;
  }
}

.footer__bottom-container {
  font-size: 13px;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;

  @media (max-width: 767px) {
  line-height: 13px;
  }
}

.footer__bottom-item {
  &:nth-child(4) {
  text-align: right;

  @media (max-width: 767px) {
  text-align: center;
  }
  }
}
